import React, {useEffect, useState} from 'react';
import { Table } from 'antd';

const columnInDrawer = [
    {
        title: ' ',
        dataIndex: 'flatNumber',
    }
]

const dataSourse = [
    {
        key: '0',
        flatNumber: '1 ',
    },
    {
        key: '1',
        flatNumber: '2 ',
    },
    {
        key: '2',
        flatNumber: '3 ',
    },
    {
        key: '3',
        flatNumber: '4 ',
    },
    {
        key: '4',
        flatNumber: '5 ',
    },
    {
        key: '5',
        flatNumber:'6 '}]

const DrawerTable = ({setDrawerIsOpen,setDrawerTableState,drawerIsOpen}) => {
    const [selectedRowKeys, setSelectedRowKeys] = useState([]);
    const [selectedRowIndex, setSelectedRowIndex] = useState(-1);
    useEffect(() => {
        const handleKeyPress = (event) => {
            if (event.key === 'ArrowUp') {
                if (selectedRowIndex > 0) {
                    setSelectedRowIndex(selectedRowIndex - 1);
                    setSelectedRowKeys([dataSourse[selectedRowIndex - 1].key]);
                }
            } else if (event.key === 'ArrowDown') {
                if (selectedRowIndex < dataSourse.length - 1) {
                    setSelectedRowIndex(selectedRowIndex + 1);
                    setSelectedRowKeys([dataSourse[selectedRowIndex + 1].key]);
                }
            }
        };
        document.addEventListener('keydown', handleKeyPress);
        return () => {
            document.removeEventListener('keydown', handleKeyPress);
        };
    }, [selectedRowIndex, dataSourse]);

    const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
            setDrawerTableState(selectedRows);
            setSelectedRowKeys(selectedRowKeys);
        },
        selectedRowKeys,
    };
    return (
        <div>
            <Table
                rowSelection={{
                    type: "checkbox",
                    ...rowSelection,
                }}
                columns={columnInDrawer}
                dataSource={dataSourse}
                pagination={false}
                size={"middle"}
            />
        </div>
    );
};
export default DrawerTable